'use client';

import React, { useState } from 'react';
import { useRouter, usePathname } from 'next/navigation';
import { useGlobalLoading } from '@/contexts/GlobalLoadingContext';
import { useLocaleRouter } from '@/hooks/useLocaleRouter';

interface NavigationLinkProps {
  href: string;
  children: React.ReactNode;
  className?: string;
  onClick?: () => void;
  // div ìš”ì†Œì— ì•ˆì „í•˜ê²Œ ì „ë‹¬í•  ìˆ˜ ìžˆëŠ” ì†ì„±ë“¤
  title?: string;
  'aria-label'?: string;
  'aria-describedby'?: string;
  id?: string;
  role?: string;
  tabIndex?: number;
}

// íŽ˜ì´ì§€ ì „í™˜ì„ ë¹ ë¥´ê²Œ í•˜ê¸° ìœ„í•œ ìµœì í™”ëœ ë„¤ë¹„ê²Œì´ì…˜ ë§í¬ ì»´í¬ë„ŒíŠ¸
export default function NavigationLink({
  href,
  children,
  className = '',
  onClick,
  // divì— ì•ˆì „í•œ ì†ì„±ë“¤ë§Œ ë¶„ë¦¬
  title,
  'aria-label': ariaLabel,
  'aria-describedby': ariaDescribedBy,
  id,
  role,
  tabIndex,
  ...unknownProps // ì´ì œ ì•ˆì „í•˜ì§€ ì•Šì€ ì†ì„±ë“¤ì€ ë²„ë¦¼
}: NavigationLinkProps) {
  const [isNavigating, setIsNavigating] = useState(false);
  const { setIsLoading } = useGlobalLoading();
  const router = useRouter();
  const pathname = usePathname();
  const { extractLocaleFromPath, getLocalizedPath, currentLocale } = useLocaleRouter();
  
  const handleClick = (e: React.MouseEvent) => {
    e.preventDefault(); // ê¸°ë³¸ ë§í¬ ë™ìž‘ ë°©ì§€
    
    // í˜„ìž¬ ê²½ë¡œì™€ íƒ€ê²Ÿ ê²½ë¡œë¥¼ ì–¸ì–´ ê²½ë¡œë¥¼ ê³ ë ¤í•˜ì—¬ ë¹„êµ
    const { path: currentCleanPath } = extractLocaleFromPath(pathname);
    const { path: targetCleanPath } = extractLocaleFromPath(href);
    
    // hrefê°€ ë¡œì¼€ì¼í™”ë˜ì§€ ì•Šì€ ê²½ìš° í˜„ìž¬ ë¡œì¼€ì¼ë¡œ ë¡œì¼€ì¼í™”
    const normalizedTargetHref = href.startsWith(`/${currentLocale}/`) 
      ? href 
      : getLocalizedPath(href, currentLocale);
    
    console.log('ðŸ” [NavigationLink] Link click:', {
      href,
      normalizedTargetHref,
      currentPath: pathname,
      currentCleanPath,
      targetCleanPath,
      isSamePage: currentCleanPath === targetCleanPath,
      currentLocale
    });
    
    // ê°™ì€ íŽ˜ì´ì§€ì¸ì§€ í™•ì¸ (ì–¸ì–´ ê²½ë¡œ ì œì™¸í•˜ê³  ë¹„êµ)
    if (currentCleanPath === targetCleanPath) {
      console.log('ðŸ” [NavigationLink] Same page detected, cancelling navigation');
      
      // ì‚¬ìš©ìž ì •ì˜ onClick ì½œë°±ì€ ì‹¤í–‰ (ë©”ë‰´ ë‹«ê¸° ë“±ì˜ ë™ìž‘ì„ ìœ„í•´)
      if (onClick) {
        onClick();
      }
      
      // ë„¤ë¹„ê²Œì´ì…˜ì€ ì·¨ì†Œ
      return;
    }
    
    // ë‹¤ë¥¸ íŽ˜ì´ì§€ì¸ ê²½ìš° ë¡œë”© ì‹œìž‘ ë° ë„¤ë¹„ê²Œì´ì…˜ ì§„í–‰
    console.log('ðŸ” [NavigationLink] Starting loading for navigation to:', normalizedTargetHref);
    setIsLoading(true);
    setIsNavigating(true);
    
    // í”„ë¡œê·¸ëž˜ë§¤í‹± ë„¤ë¹„ê²Œì´ì…˜ (ì •ê·œí™”ëœ href ì‚¬ìš©)
    router.push(normalizedTargetHref);

    // ì‚¬ìš©ìž ì •ì˜ onClick ì½œë°± ì‹¤í–‰
    if (onClick) {
      onClick();
    }
  };
  
  // divì— ì•ˆì „í•œ ì†ì„±ë“¤ë§Œ ì „ë‹¬
  const safeDivProps = {
    title,
    'aria-label': ariaLabel,
    'aria-describedby': ariaDescribedBy,
    id,
    role: role || 'button', // ì ‘ê·¼ì„±ì„ ìœ„í•´ role="button" ê¸°ë³¸ê°’
    tabIndex: tabIndex || 0, // í‚¤ë³´ë“œ ì ‘ê·¼ì„±ì„ ìœ„í•´ tabIndex ê¸°ë³¸ê°’
  };
  
  // í‚¤ë³´ë“œ ì´ë²¤íŠ¸ í•¸ë“¤ëŸ¬ (Enter, Space í‚¤ ì§€ì›)
  const handleKeyDown = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' || e.key === ' ') {
      e.preventDefault();
      handleClick(e as any);
    }
  };
  
  return (
    <div 
      onClick={handleClick}
      onKeyDown={handleKeyDown}
      className={`${className} ${isNavigating ? 'opacity-90' : 'opacity-100'} transition-opacity duration-200 cursor-pointer`}
      style={{ userSelect: 'none' }}
      {...safeDivProps}
    >
      {children}
    </div>
  );
} 